.hong-alert {
    width: 100%;
    padding: 8px 16px;
    margin: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border-radius: 4px;
    position: relative;
    background-color: #fff;
    overflow: hidden;
    opacity: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-transition: opacity .2s;
    transition: opacity .2s
}

.hong-alert.is-center {
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center
}

.hong-alert--success {
    background-color: #f0f9eb;
    color: #67c23a
}

.hong-alert--success .hong-alert__description {
    color: #67c23a
}

.hong-alert--info {
    background-color: #f4f4f5;
    color: #909399
}

.hong-alert--info .hong-alert__description {
    color: #909399
}

.hong-alert--warning {
    background-color: #fdf6ec;
    color: #e6a23c
}

.hong-alert--warning .hong-alert__description {
    color: #e6a23c
}

.hong-alert--error {
    background-color: #fef0f0;
    color: #f56c6c
}

.hong-alert--error .hong-alert__description {
    color: #f56c6c
}

.hong-alert__content {
    display: table-cell;
    padding: 0 8px
}

.hong-alert__icon {
    font-size: 16px;
    width: 16px
}

.hong-alert__icon.is-big {
    font-size: 28px;
    width: 28px
}

.hong-alert__title {
    font-size: 13px;
    line-height: 18px
}

.hong-alert__title.is-bold {
    font-weight: 700
}

.hong-alert .hong-alert__description {
    font-size: 12px;
    margin: 5px 0 0
}

.hong-alert__closebtn {
    font-size: 12px;
    color: #c0c4cc;
    opacity: 1;
    position: absolute;
    top: 12px;
    right: 15px;
    cursor: pointer
}

.hong-alert__closebtn.is-customed {
    font-style: normal;
    font-size: 13px;
    top: 9px
}

.hong-alert-fade-enter,
.hong-alert-fade-leave-active {
    opacity: 0
}